export default class RedrawNav {
    constructor(el, filter) {
        this.el = el;
        if(filter) this.filter = filter.querySelectorAll('.sl-prod__filter-type');

        this.itemsNav = [...this.el.querySelectorAll('.nav__item')];
        this.subList = this.el.querySelector('.nav__sub-menu-coffee-list');

        this.itemsPoints = [];
        
        this.subActive = null;
    }

    initNav() {
        // собираем блоки к которым будем прокручивать страницу
        this.itemsNav.forEach(item => {
            const flag = item.dataset.item
            const el = document.querySelector(`.${flag}`);
            this.itemsPoints.push(el);
        })
        
        if(this.filter) {
            [...this.filter].forEach(item => {
                console.log(item)
                const el = this.createEl('li', ['nav__sub-menu-coffee-item']);
                el.textContent = item.textContent;
                this.subList.append(el);
            })
        }
    }

    scrolling(flag) {
        // наличие filter говорит о том что это верхнее меню
        if(flag === 'coffee' && this.filter) return;

        const el = this.itemsPoints.find(item => item.matches(`.${flag}`));

        el.scrollIntoView({
            behavior: "smooth",
            block: "start",
            inline: "nearest"
        })        
    }

    openSubMenu(flag) {

    }

    createEl(el, arrClassName) {
        const element = document.createElement(el);

        arrClassName.forEach(c => element.classList.add(c));
        
        return element;
    }
}